import React from "react";
import ConnectButton from "./ConnectButton";

export default function Header() {
  return (
        <>
            {/* Header */}
            <div style={{marginTop: "30px"}}>
                <div className="row" >
                    <div className="col-0"></div>
                    <div className="col-12">
                        <div className="float-right">
                            <ConnectButton/>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}
