import { useEffect, useState, useCallback } from 'react';
import { getBalance } from 'utils/contract';
import { useSelector, useDispatch } from 'react-redux';
import {
    setConnected,
    connectState,
    disconnected,
} from '../../app/reducers/walletSlice';
import VuiButton from "components/VUI/VuiButton";

function ConnectButton() {
    const [wallet, setWallet] = useState("0x0");
    const [balance, setBalance] = useState(0);

    const connected = useSelector(connectState);
    const dispatch = useDispatch();

    const checkWalletIsInstalled = () => {
        const { ethereum } = window;

        if (!ethereum) {
            alert("Install MetaMask");
            return false;
        } else {
            console.log("Ready to go");
            return true;
        }
    }

    const connectWalletHandler = useCallback(async () => {
        const { ethereum } = window;
        if(!checkWalletIsInstalled()) return;
        
        ethereum.on('accountsChanged', async function (accounts) {
            let wallet = accounts[0];
            if (wallet) {
                let balance = await getBalance(wallet);
                setWallet(wallet);
                setBalance(balance);
                dispatch(setConnected(wallet.toString()))
            } else {
                await disconnectWalletHandler();
            }
        });
        ethereum.on('networkChanged', async function (networkId) {
            if(networkId !== '0x61') {
                disconnectWalletHandler();
            }
        })
          

        try {
            const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
            const chainId = await window.ethereum.request({ method: 'eth_chainId'});
            console.log(chainId);
            if(chainId === '0x61') {
                let wallet = accounts[0];
                let balance = await getBalance(wallet);
                setWallet(wallet);
                setBalance(balance);
                dispatch(setConnected(wallet.toString()))
            } else {
                alert("Connect to BSC");
            }
        } catch (err) {
            console.log(err);
        }
    }, [connected, dispatch]);

    const disconnectWalletHandler = async () => {
        setWallet('0x0');
        dispatch(disconnected());
    }

    const connectWalletButton = () => {
        return (
            <VuiButton onClick={connectWalletHandler} variant="contained" color="info" >
                Connect Wallet
            </VuiButton>
        )
    }

    const disconnectWalletButton = () => {
        
        return (
            <div>
                <VuiButton variant="contained" color="info" mr={2}>
                    {(balance/1000000000000000000).toFixed(2)}
                </VuiButton>
                <VuiButton variant="contained" color="info" mr={2}>
                    {wallet.substring(0, 6)}...{wallet.substring(38, 42)} 
                </VuiButton>
                <VuiButton onClick={disconnectWalletHandler} variant="contained" color="info" >
                    Disconnect Wallet
                </VuiButton>
            </div>
        )
    }
  
    useEffect(() => {
        checkWalletIsInstalled();
    }, [])

    return (
        <div>
            {!connected ? connectWalletButton() : disconnectWalletButton()}
        </div>
    )
}

export default ConnectButton;